import { useState, useEffect } from "@wordpress/element";
import {
  BaseControl,
  Dropdown,
  Tooltip,
  ColorPicker,
  Button,
} from "@wordpress/components";

const ColorControl = ({ label, color, onChange }) => {
  const [bgColor, setBgColor] = useState(color);

  useEffect(() => {
    onChange(bgColor);
  }, [bgColor]);

  const colorStyles = {
    padding: 2,
    borderRadius: 0,
    background: "white",
    border: "1px solid #ebebeb",
  };

  return (
    <BaseControl label={label || ""} className="eb-color-base">
      <Dropdown
        renderToggle={({ isOpen, onToggle }) => (
          <Tooltip text={color || "default"}>
            <div className="eb-color-ball" style={color && colorStyles}>
              <div
                style={{
                  height: 16,
                  width: 16,
                  borderRadius: "0%",
                  boxShadow: "inset 0 0 0 1px rgba(0,0,0,.1)",
                  backgroundColor: bgColor,
                }}
                aria-expanded={isOpen}
                onClick={onToggle}
                aria-label={color || "default"}
              ></div>
            </div>
          </Tooltip>
        )}
        renderContent={() => (
          <ColorPicker
            color={color}
            onChangeComplete={({ rgb }) => {
              setBgColor(`rgba(${rgb.r},${rgb.g},${rgb.b},${rgb.a})`);
            }}
          />
        )}
      />
      {bgColor && (
        <Button
          isSmall
          className="eb-color-undo"
          icon="image-rotate"
          style={{
            transform: "scaleX(-1) rotate(90deg)",
          }}
          onClick={() => setBgColor(undefined)}
        ></Button>
      )}
    </BaseControl>
  );
};

export default ColorControl;
